<template>
  <div class="c-discuss">

    <div class="discuss-top__wrap">
      <div class="discuss-top top-box content-box">
        <official-account />
      </div>
    </div>
    <div class="c-discuss__wrap">
      <div class="discuss-body content-box">
        <div class="discuss__title">
          <div class="h1"
               v-html="$t('discuss.h1')"></div>
        </div>
        <div class="discuss__break">
          <img class="www"
               src="../../public/static/img/wave1.png">
        </div>
        <div class="discuss__list"
             v-for="(item,index) of list"
             :key="index">
          <div class="row discuss__intro">
            <div class="col-xs-12 col-sm-4 col-md-4 col-lg-4 left">
              <div class="left__cover">
                <img :src="coverList[index]">
                <span class="bd-box bd_n1"></span>
                <span class="bd-box bd_n2"></span>
                <span class="bd-box bd_n3"></span>
                <span class="bd-box bd_n4"></span>
              </div>
            </div>
            <div class="col-xs-12 col-sm-8 col-md-8 col-lg-8 right"
                 :class="[index==list.length-1 ? 'last':'']">
              <div class="right__content">
                <div class="text">
                  <div class="discuss__title"
                       v-html="$t(`discuss.${item}.title`)"></div>
                  <p class="discuss__skill">{{$t(`discuss.${item}.skill`)}}</p>
                  <div class="discuss__time"
                       v-html="$t(`discuss.${item}.time`)"></div>
                  <div class="discuss__des"
                       v-html="$t(`discuss.${item}.des`)"></div>
                  <div class="discuss__duty"
                       v-html="$t(`discuss.${item}.duty`)"></div>
                  <div class="discuss__git">
                    <a :href="$t(`discuss.${item}.git`)"
                       target="blank">GIT</a>
                  </div>

                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Footer />
  </div>
</template>
<script>
import Footer from '@/components/Footer'
import OfficialAccount from '@/components/OfficialAccount'
// import { MixinSkrollr } from '@/utils/mixin'
export default {
  components: { Footer, OfficialAccount },
  data() {
    return {
      coverList: [
        require('../../public/static/img/bird.png'),
        require('../../public/static/img/logistics.png'),
        require('../../public/static/img/music.png'),
        require('../../public/static/img/blog.png')
      ],
      list: ['bird', 'logistics', 'music', 'blog']
    }
  }
  // mixins: [MixinSkrollr]
}
</script>
<style scoped lang="scss">
.c-discuss {
  background: #000;
  &__wrap {
    width: 100%;
    // background-image: url(data:image/svg+xml;base64,);
    background-size: cover;
  }
}
.discuss-top {
  height: 260px;
  display: table;
  &__wrap {
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    border-top: 1px solid rgba(255, 255, 255, 0.1);
  }
}
.content-box {
  width: 1080px;
  max-width: 100%;
  margin: 0 auto;
}
.discuss-body {
  // background: #000;
  padding: 100px 0;
  .discuss__title {
    text-align: left;
    color: #fff;
    .h1 {
      font-family: Trade Gothic W1;
      font-size: $h1;
      line-height: $h1_lh;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: 10px;
      text-align: center;
    }
  }
  .discuss__break {
    position: relative;
    text-align: center;
    padding: 50px 0 0;
    img {
      // margin-top: 20px;
      width: 130px;
      height: 30px;
    }
  }

  .discuss__intro {
    position: relative;
    font-family: 'Lato', sans-serif;
    font-size: 12px;
    line-height: 2;
    color: #afafaf;
    letter-spacing: 1px;
    text-align: justify;
    padding: 100px 40px 0;
    p {
      margin-bottom: 1em;
    }
    &:hover .bd-box {
      display: block;
    }
  }
  .left {
    img {
      width: 100%;
    }
    .left__cover {
      position: relative;
      padding: 40px 0;
    }
    .bd-box {
      background: #ffffff;
      position: absolute;
      display: none;
      &.bd_n1 {
        animation: bdA1 1.5s ease-out 0s infinite;
        width: 0;
        height: 4px;
        top: 0;
        left: 0;
      }
      &.bd_n2 {
        animation: bdA2 1.5s ease-out 0s infinite;
        width: 4px;
        height: 0;
        top: 0;
        right: 0;
      }
      &.bd_n3 {
        animation: bdA3 1.5s ease-out 0s infinite;
        width: 0;
        height: 4px;
        bottom: 0;
        right: 0;
      }
      &.bd_n4 {
        animation: bdA4 1.5s ease-out 0s infinite;
        width: 4px;
        height: 0;
        bottom: 0;
        left: 0;
      }
    }
  }
  .right {
    padding: 0 40px;
  }
  .right__content {
    text-align: justify;
    overflow: hidden;
    p {
      font-family: 'Lato', sans-serif;
      font-size: 12px;
      line-height: 2;
      color: #afafaf;
      letter-spacing: 1px;
    }
    .discuss__title {
      text-align: left;
      line-height: 1;
      margin-bottom: 1em;
      line-height: 1.5em;
      font-family: 'Trade Gothic W01';
      font-weight: 700;
      font-size: 20px;
      letter-spacing: 5px;
      color: #fff;
    }
  }
}
@keyframes bdA1 {
  0% {
    width: 0;
    left: 0;
  }

  55% {
    width: 100%;
    left: 0;
  }
  100% {
    width: 0;
    left: 100%;
  }
}
@keyframes bdA2 {
  0% {
    height: 100%;
    top: 0;
  }
  54% {
    height: 0;
    top: 100%;
  }
  55% {
    height: 0;
    top: 0;
  }
  100% {
    height: 100%;
    top: 0;
  }
}
@keyframes bdA3 {
  0% {
    width: 0;
    right: 0;
  }
  55% {
    width: 100%;
    right: 0;
  }
  100% {
    width: 0;
    right: 100%;
  }
}
@keyframes bdA4 {
  0% {
    height: 100%;
    bottom: 0;
  }
  54% {
    height: 0;
    bottom: 100%;
  }
  55% {
    height: 0;
    bottom: 0;
  }
  100% {
    height: 100%;
    bottom: 0;
  }
}

@media screen and (max-width: 767px) and (min-width: 0px) {
  .c-discuss .discuss-body {
    padding: 50px 0;
  }
  .discuss-body .discuss__intro {
    padding: 50px 20px 0;
    margin: 0;
  }
  .c-discuss .content-box {
    width: 100%;
  }
  .c-discuss .top-box {
    height: 90px;
  }
  .c-discuss .discuss__intro .left__cover {
    width: 50%;
    padding: 0;
    margin: 0 auto;
  }
  .c-discuss .discuss__intro .right {
    margin-top: 20px;
    padding-top: 20px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    &.last {
      border: none;
    }
  }

  .c-discuss .discuss-body .discuss__title .h1 {
    padding: 0 20px;
    font-size: 32px;
    letter-spacing: 7px;
    line-height: 1.2em;
  }
  .c-discuss .discuss__intro .discuss__title {
    font-size: 16px;
  }
}
</style>
